import { RouterModule, Routes } from '@angular/router';
import { AppComponents } from './components/app/app.components';
import { CasoComponent } from './components/caso/caso.component';
import { ContactoComponent } from './components/contacto/contacto.component';
import { GreenComponent } from './components/green/green.component';
import {HomeComponent } from './components/home/home.component';
import { LoginComponent } from './components/login/login.component';
import { NosotrosComponent } from './components/nosotros/nosotros.component';
import { SignupComponent } from './components/signup/signup.component';
import { TiendaComponent } from './components/tienda/tienda.component';

const APP_ROUTES: Routes = [
    {path: 'home', component: HomeComponent},
    {path: 'nosotros', component: NosotrosComponent},
    {path: 'tienda', component: TiendaComponent},
    {path: 'contacto', component: ContactoComponent},
    {path: 'login', component: LoginComponent},
    {path: 'signup', component: SignupComponent},
    {path: 'app', component: GreenComponent},
    {path: 'caso', component: CasoComponent},
    {path: '**', pathMatch: 'full', redirectTo: 'home'}
];

export const APP_ROUTING = RouterModule.forRoot(APP_ROUTES, {useHash: true, relativeLinkResolution: 'legacy'});
