import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroupDirective, NgForm, Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import * as moment from 'moment';
import { UsersService } from 'src/app/services/users.service';

export class MyErrorStateMatcher implements ErrorStateMatcher{
  isErrorState(control: FormControl | null, form: FormGroupDirective | NgForm | null): boolean{
    const isSubmitted = form && form.submitted;
    return !!(control && control.invalid && (control.dirty || control.touched || isSubmitted));
  }
}

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.css']
})
export class SignupComponent implements OnInit {
  hide = true;

  nombreFormControl = new FormControl('', [
    Validators.required
  ]);
  appaternoFormControl = new FormControl('', [
    Validators.required
  ]);
  emailFormControl = new FormControl('', [
    Validators.required,
    Validators.email
  ]);
  passwordFormControl = new FormControl('', [
    Validators.required,
    Validators.minLength(5),
  ]);
  fechanacFormControl = new FormControl('', [
    Validators.required,
  ]);
  nicknameFormControl = new FormControl('', [
    Validators.required,
  ]);
  matcher = new MyErrorStateMatcher();

  constructor(private userService: UsersService) {    }

  ngOnInit(): void {
  }

   public signUp(): void {
     const body: any = {
        name: this.nombreFormControl.value,
        last_name: this.appaternoFormControl.value,
        birthdate: moment(this.fechanacFormControl.value).format(),
        email: this.emailFormControl.value,
        password: this.passwordFormControl.value,
        nickname: this.nicknameFormControl.value
      };
     this.userService.createUser(body)
      .subscribe(res => {
        if (res.message === 'User created'){
          alert('Usuario creado con éxito. Guardaremos tus datos y te contactaremos cuando el producto esté a la venta');
        }else{
          alert('Algo salió mal, inténtalo nuevamente');
        }
      });
   }
}
